/* image grid styles */

/* modal styles */

.icon_compass {
  font-size: 40px;
  color: #ff414d;
  margin-right: 5px;
  align-items: center;
  display: block;
  margin-top: -10px;
}

.app__navbar {
  position: absolute;
  background: transparent !important;
  top: 50px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);

  z-index: 3;
  width: 90%;
}
.navbar-dark span {
  border: none !important;
  font-size: 30px;
}

.link {
  color: rgba(255, 255, 255, 0.7) !important;
  text-transform: uppercase;
  font-family: var(--fontfamily);
  font-size: 13px;
  padding: 0.5rem 25px;
  font-weight: 700;
  letter-spacing: 2px;
}
.navbar-nav {
  grid-gap: 1.4rem;
  gap: 1.4rem;
}
.brand_title {
  color: #fff;
  line-height: 1.1;
  display: block;
  font-weight: 900;
  font-size: 31px;
  font-family: var(--fontfamily);
}

.navbar-collapse {
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: flex-end;
}
.brand_title small {
  display: block;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 11px;
}
/* add listing button */
.add_listing {
  background: #ff414d !important;
  border: 1px solid #ff414d !important;
  color: #fff !important;
  padding: 0.5rem 12px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 600;

  text-transform: uppercase;

  letter-spacing: 1px;
  cursor: pointer;
  border-radius: 0.25rem !important;
}
.add_listing:hover {
  background: #f66872 !important;
}

@media (max-width: 980px) {
  .app__navbar {
    background: #151c26 !important;
    position: relative;
    top: 0;
    width: 100%;
    padding: 7px 0;
  }
  .link {
    color: #fff !important;
  }
  .add_listing {
    width: 150px;
  }
}

@media (max-width: 470px) {
  .icon_compass {
    font-size: 25px;
  }
  .brand_title {
    font-size: 16px;
  }
  .navbar-dark span {
    font-size: 15px;
  }
  .brand_title small {
    font-size: 8px;
  }
}

.app__hero {
  background: url("https://preview.colorlib.com/theme/directone/images/xbg_1.jpg.pagespeed.ic.EiRxZF1ff8.webp")
    no-repeat center center/cover;
  height: 100vh;
  position: relative;
  z-index: -1;
  min-height: 750px;
  background-attachment: fixed;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;

  opacity: 0.3;
  background: #000;
  z-index: -1;
}
.slider-text {
  height: 100vh;
  min-height: 750px;
  color: rgba(255, 255, 255, 0.8);
}
.slider-text h1 {
  font-weight: 700;
  color: #fff;
  font-size: 64px;
  line-height: 1.2;
  font-family: var(--fontfamily);
}
.slider-text p {
  margin-bottom: 1rem;
}
.search-property-1 {
  background-color: #fff;
  border-radius: 5px;
  overflow-x: hidden;
}
.search-property-1 .form-group {
  width: 100%;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 0;
  font-family: var(--fontfamily);
}
.form-control {
  border: none !important ;
  padding: 0 !important;
  padding-left: 20px !important;
  display: block;
  height: inherit !important;
  border-radius: 0;
  color: rgba(0, 0, 0, 0.4) !important;
  font-size: 14px !important;
}
.form-control:focus {
  border: none !important;
  box-shadow: none !important;
  outline: none !important;
}

.form-field .btn {
  cursor: pointer;
  background-color: #ff414d;
  border: none !important;
}
.form-field {
  position: relative;
}
.btn div {
  margin-right: 4px;
  font-size: 13px;
}
.form-field .btn {
  cursor: pointer;
}
.search {
  border: none;
  background: transparent !important;
  color: #fff;
  font-weight: 700;
  cursor: pointer !important;
}
.icon {
  position: absolute;

  top: 46%;
  left: 0;
  font-size: 16px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: rgba(0, 0, 0, 0.1);
}
@media (max-width: 520px) {
  .slider-text h1 {
    font-size: 25px !important;
    margin-top: 4rem;
  }
  .slider-text p {
    font-size: 10px;
  }
}
@media (max-width: 768px) {
  .slider-text h1 {
    font-size: 54px;
    margin-top: 4rem;
  }
  .search-property-1 {
    margin-top: 40px;
  }
}

.app__facilities {
  background-color: #f0f4f7 !important;
  position: relative;
}
.wrapper {
  margin-top: -80px !important;
}

.facility-content {
  background-color: #fff;
  border-radius: 4px;
  padding: 30px;
  transition: 0.5s all ease-in;
  height: 100%;
}

.facility-icon {
  font-size: 60px;
  line-height: 1.2;

  color: #ff414d;
}
.facility h3 {
  font-size: 1.2rem;
  text-transform: capitalize;
  font-family: var(--fontfamily);
  font-weight: 600;
  color: #000000cc;
}
.listing {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.5);
}

.listing span {
  color: #ff414d;
  padding: 2px 5px;
  background-color: rgba(0, 0, 0, 0.03);
  border-radius: 5px;
}

.app__listing {
  background-color: #f0f4f7 !important;
  padding: 7rem 0;
}

.card-container {
  background-color: #fff;
  box-shadow: 0px 5px 21px -14px rgb(0 0 0 / 14%);
}
.app__card {
  padding: 0 0.6rem;
}
.img {
  background-image: url("https://preview.colorlib.com/theme/directone/images/xlist-1.jpg.pagespeed.ic.yWFT-XXoeF.webp");
  width: 100%;
  height: 250px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.card-content {
  padding: 45px 25px;
}
.card-container .res-name {
  font-size: 1.3rem;
  font-weight: 600;
  text-transform: capitalize;
}
.card-container .food-type {
  color: #ff414d;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
}

.card-container .address {
  color: #999;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}
.card-container .contact_no {
  color: #999;
  margin-bottom: 0;
  font-size: 0.9rem;
}
.address .location_icon {
  color: #ff414d;
  margin-right: 0.3rem;
}
.contact_no .phone_icon {
  color: #ff414d;
  margin-right: 0.3rem;
}
.border_bottom {
  border-top: 1px solid rgba(0, 0, 0, 0.07);
  margin-top: 3rem;
}
.review {
  width: calc(100% - 5px);
  text-align: left;
  font-size: 0.9rem;
  font-family: var(--fontfamily);
  color: #999;
}
.rattings {
  margin: 0 0.4rem;
  color: #000;
}
.no_of_reviews {
  color: #000;
}
.heart-icon {
  color: #ff414d;
  background: rgba(0, 0, 0, 0.04);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  transition: all 0.3 ease-in;
  padding-top: 0.1rem;
}
.heart-icon:hover {
  background-color: #ff414d;
  color: #fff;
  cursor: pointer;
}
.star_icon {
  color: #ffc107;
  font-size: 11px;
  display: inline-block;
  padding-top: -10px;
}

.subheading {
  font-size: 0.9rem;
  color: #ff414d;
  text-transform: uppercase;
  font-weight: 700;
  font-family: var(--fontfamily);
}

.heading {
  font-size: 3.1rem;
  font-family: var(--fontfamily);
  text-transform: capitalize;
  font-weight: 700;
}

.color_white {
  color: #fff;
}
@media (max-width: 520px) {
  .heading {
    font-size: 2rem;
  }
}

.app__banner {
  background-color: #f0f4f7 !important;
}
.content {
  padding: 5rem 3rem;
  background-image: linear-gradient(
    129deg,
    #ff414d 25%,
    #f03943 25%,
    #f03943 50%,
    #ff414d 50%,
    #ff414d 75%,
    #f03943 75%,
    #f03943 100%
  );
